import React from "react"
import { Helmet } from "react-helmet";
import Link, { navigateTo } from 'gatsby-link';

import * as PropTypes from "prop-types"

class PostTemplate extends React.Component {
	constructor(props) {
		super(props);
		this.handleClick = this.handleClick.bind(this);
	}
	
	static propTypes = {
    	data: PropTypes.shape({
      		jobsJson: PropTypes.object.isRequired,
    	}),
	}

	handleClick (evt) {
		navigateTo({
			pathname: '/apply',
			state: {
			  id: this.props.data.jobsJson.id,
			  path: this.props.data.jobsJson.positionSlug,
			  name: this.props.data.jobsJson.position,
			  place:  this.props.data.jobsJson.place,
			  jobId: this.props.data.jobsJson.jobId,
			  referer: this.props.location.state === undefined ? 'https://www.gojek.io/' : this.props.location.state.referer
			},
		  })
	}

	getTabHeading(key) {
		switch(key) {
			case 'responsibilities': 
				return "Responsibilities";
			case 'experience':
				return "EXPERIENCE & SKILLS"
			case 'about':
				return "About you"
			case 'overview':
				return "Overview"
			case 'expectations':
				return "Expectations"
			case 'whatWillYouDo':
				return "What will you do"
			case 'whatWillYouNeed':
				return "What will you need"
			case 'whatWouldbeaBonus':
				return "What would be a bonus"
			case 'projects':
				return "Projects you could work on"
			case 'requirements':
				return "Requirements"
			case 'jobDescription':
			return "Job Description"
			default:
				return "About";
			
		}
	}

	render() {
		const { jobsJson } = this.props.data;
		return (
			<div>
				<Helmet>
					<title> { jobsJson.title } </title>
					<meta property="og:title" content={ jobsJson.title } />
					<meta name="twitter:title" content={ jobsJson.title } />
					<meta name="description" content={ jobsJson.metaDescription } />
					<meta name="twitter:description" content={ jobsJson.metaDescription } />
					<meta property="og:description" content={ jobsJson.metaDescription } />
				</Helmet>

				<section className="first-section">
					<img className="img-fluid" src="../../images/careers/job-illustration.png" />
				</section>
				<section className="">
					<div className="container py-5">
						<div className="row">
							<div className="col-lg-10 offset-lg-1">
								<h1 className="h2 font-xl text-left text-black raleway-extrabold font-xl text-black mb-1">{jobsJson.position}</h1>
								<div className="text-left">
											<button
										onClick={ this.handleClick}
										target = "_blank"
										className = "text-center btn bg-green text-white raleway-bold text-uppercase my-1 font-md custom-btn"
									>
										APPLY NOW
									</button>
								</div>
							</div>
						</div>
					</div>
				</section>

				<section className="">
					<div className="container">
						<div className="row">
							<div className="col-lg-10 offset-lg-1">
								<ul className="nav nav-tabs" id="myTab" role="tablist">
								{jobsJson.headings.map((heading, key) => {
									return(
										<li className="nav-item" key={key} >
											<a className={"nav-link jd-tab roboto-bold text-uppercase " + (key === 0 ? 'active' : '')} id={`#${heading}-tab`} data-toggle="tab" href={`#${heading}`} role="tab" aria-controls={`${heading}`}
											aria-selected="true">{this.getTabHeading(heading)}</a>
										</li>
									)
								}
								)}
							
								{
									jobsJson.jobId==='fk0jsih'&&
									<li>
											<a className={"nav-link jd-tab roboto-bold text-uppercase "} 
											id={`#Requirements-tab`} data-toggle="tab" href={`#Requirements`} role="tab" aria-controls={`Requirements`}
											aria-selected="true"> Requirements</a>
									</li>
								}
								
								
								</ul>
							</div>
						</div>

						<div className="tab-content" id="myTabContent">
							{jobsJson.headings.map((heading, key) => {
								return(
									<div key={key} className={"tab-pane fade show " + (key === 0 ? 'active' : '')} id={`${heading}`} role="tabpanel" aria-labelledby={`${heading}-tab`}>
										<section className="roboto-regular">
											<div className="container">
												<div className="row justify-content-center">
													<div className="col-lg-10">
														<div className="row pt-4"> 
														{
															(jobsJson.jobId === 'fk019mp' && heading === 'responsibilities') &&
															<p>{ jobsJson.responsibilitiesOverview }</p>
														}
														{
															(heading !== "overview") &&
															<ul className="text-green" >
																<div className="row" >
																	{
																		jobsJson[heading].map((value, key) => {
																		return(
																			<div className="col-md-6 col-sm-12  pr-5" key={key} >
																			{
																				(key===0&&heading === 'requirements'&& (jobsJson.jobId==='fk0j3ij'||jobsJson.jobId==='fk0j3ix'))&&
																				<li className="py-2 font-md">
																					<span className="font-sm text-dark" > Your toolkit:</span>
																					<ul>
																						<li className="py-2 font-md">
																							<span className="font-sm text-dark" > Microservices (HTTP &amp; GRPC)</span>
																						</li>
																						<li className="py-2 font-md">
																							<span className="font-sm text-dark" > Experience in Golang, Ruby, Clojure, Java (and a willingness to learn others).
																							</span>
																						</li>
																						<li className="py-2 font-md">
																							<span className="font-sm text-dark" > Postgresql
																							</span>
																						</li>
																						<li className="py-2 font-md">
																							<span className="font-sm text-dark" > Devops: Chef, Ansible, Docker, Kubernetes
																							</span>
																						</li>
																						<li className="py-2 font-md">
																							<span className="font-sm text-dark" > Rails, Javascript
																							</span>
																						</li>
																						<li className="py-2 font-md">
																							<span className="font-sm text-dark" > Kafka, Spark, Flink
																							</span>
																						</li>
																					</ul>
																				
																				</li>
																			}
																				<li key = {key} className="py-2 font-md" >
																					<span className="font-sm text-dark" > {value}</span>
																				</li>
																				
																				
																			</div>
																		)}
																	)}
																	
																</div>
															</ul>
															}
															{
																heading === "overview" &&
																<div className="col-12">
																	<p className="pl-3  	text-lg-left 
																	font-sm">
																		{jobsJson.overview}
																	</p>
																	<div className="row">
																	<div className="col-md-6">
																	<p className="pl-3 font-sm  text-lg-left text-center ">{jobsJson.rolesOverview} </p>
																	{/* {
																		jobsJson.roles.length > 0 && 
																	
																		<ul className=" text-green">
																			{
																				jobsJson.roles.map((role, key) => (
																					<li className="py-2 font-md" key={key}>
																						<span className="font-sm text-dark" > {role} </span>
																					</li>
																				))
																			}
																		</ul>
																	} */}
																		</div>
																		<div className="col-md-6">
																	<p className="pl-3 font-sm  text-lg-left text-center ">{jobsJson.scopeOverview} </p>
																	{/* {
																		jobsJson.scope.lenght > 0 &&
																		<ul className=" text-green">
																			{
																				jobsJson.scope.map((role, key) => (
																					<li className="py-2 font-md" key={key}>
																						<span className="font-sm text-dark" > {role} </span>
																					</li>
																				))
																			}
																		</ul>
																		} */}
																		</div>
																	</div>
																	</div>
																	
															}
															

														</div>
														{
															(jobsJson.jobId !== 'fk019mp' && heading+'Overview' !== "")  &&
															<p className="font-md text-dark">
																{ jobsJson[heading+'Overview'] }
															</p>
														}
													</div>
												</div>
											</div>
										</section>
									</div>

									
								)}
							)}	
							
							{
										jobsJson.jobId === 'fk0jsih'&&
										<div  className={"tab-pane fade show " } id={`Requirements`} role="tabpanel" aria-labelledby={`Requirements-tab`}>
										<section className="roboto-regular">
								<div className="container">
									<div className="row justify-content-center">
									<div className="col-lg-10">
										<div className="row pt-4"> 
										<p className="pl-4 text-green font-lg mb-0 font-weight-bold">Mandatory</p>
										<ul className="text-green" >
												<div className="row" >
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Working knowledge of the general mobile landscape, architectures, trends, and emerging technologies
															</span>
														</li>
														
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > At least 3-4 years of experience in Android app development
															</span>
														</li>
													</div>

													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Strong understanding of Android Apps architecture and implementation
															</span>
														</li>
														
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" >Prior experience in building SDKs
															</span>
														</li>
													</div>

													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" >Proficiency in Kotlin, Git, and Gradle
															</span>
														</li>
														
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" >Ability to write product specifications and actively participate in the planning phase
															</span>
														</li>
													</div>

													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Ability to write model code for functional and nonfunctional requirements and help improve the code quality standard across the team
															</span>
														</li>
														
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Working experience with popular third-party libraries for networking, async, image loading etc.
															</span>
														</li>
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Ability to create extendable and reusable ecosystems &amp;platforms and mobile.
															</span>
														</li>
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Well versed with Agile methodologies, TDD and Test Engineering and Automation.
															</span>
														</li>
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" >Solid understanding of implementing Continuous Integration and Continuous Delivery principles.
															</span>
														</li>
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Good debugging capabilities and understanding of the device fragmentation in Android
															</span>
														</li>
													</div>
												</div>
											</ul>

											<p className="pl-4 text-green font-lg mb-0 font-weight-bold">Nice to Have</p>
										<ul className="text-green" >
												<div className="row" >
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Prior experience in building a Chat ecosystem
															</span>
														</li>
														
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > An understanding of Android Code Obfuscation and Security
															</span>
														</li>
													</div>

													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Prior knowledge of WebSockets and background services on Android
															</span>
														</li>
														
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" >Prior knowledge of WebSockets and background services on Android
															</span>
														</li>
													</div>

													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" >Basic knowledge of iOS code structure to understand the requirements of a mobile platform holistically
															</span>
														</li>
														
													</div>
													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" >Ability to troubleshoot fuzzy problems across systems and subsystems effectively
															</span>
														</li>
													</div>

													<div className="col-md-6 col-sm-12  pr-5" >
														<li className="py-2 font-md" >
															<span className="font-sm text-dark" > Ability to design systems/subsystems, API contracts and enable platformization independently
															</span>
														</li>
													</div>
													
												</div>
											</ul>
										</div>
										</div>
										</div>
										</div>
										</section>
										</div>
							}												
						</div>
					</div>
				</section>

				<section className="bg-green roboto-regular mt-5">
					<div className="container py-5">
						<h1 className="h2 font-xl  text-center text-white">Think you fit the bill?</h1>
						<div className="text-center ">
											<button
										onClick={ this.handleClick}
										target = "_blank"
										className = " text-center btn bg-white text-green raleway-bold text-uppercase my-1 font-md challenging-button custom-btn"
									>
										APPLY NOW
									</button>
						</div>
					</div>
				</section>
			</div>
		
		)
	}
}

export default PostTemplate

export const pageQuery = graphql`
  query PostPage($positionSlug: String!) {
	# Select the post which equals this slug.
    jobsJson(positionSlug: {eq: $positionSlug }) {
		id
	  	positionSlug
		link
		place
		responsibilities
		position
		jobId
		experience
		headings
		about
		responsibilitiesOverview
		rolesOverview
		scopeOverview
		overview
		expectations
		whatWillYouDo
		whatWillYouNeed
		whatWouldbeaBonus,
		jobDescription,
		requirements,
		metaDescription,
		title
		}
	}
`


